/* YourComponent.module.css */
.container {
    min-height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
}

.image {
    height: 100vh;
    width: 70%;
}

.overlayDiv {
    position: absolute;
    top: 40px;
    right: 5%;
    bottom: 0;
    width: 50%;
    height: 45%;
    transform: translateY(40%);
    background-color: #ffffff;
    box-shadow: 2px 1px 40px 0 rgba(0, 0, 0, 0.25);
    color: #fff;
    padding: 20px 20px 20px 40px;
    z-index: 2;
}

.holderContainer {
    margin-top: 1.5rem;
}

@media screen and (max-width: 1110px) {
    .image {
        height: 100vh;
        width: 80%;
    }

    .overlayDiv {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 80%;
        height: 60%;
        transform: translateY(40%);
        background-color: #ffffff;
        color: #fff;
        padding: 20px 0 20px 0;
        z-index: 2;
    }
}

@media screen and (max-width: 796px) {
    .container {
        flex-direction: column;
        justify-content: space-around;
    }

    .image {
        width: 100%;
        height: 60%;
        display: none;
    }
    .imageMobile{
        width: 100%;
    }
    .overlayDiv {
        padding: 20px 0 20px 0;
        left: 0;
        width: 100% !important;
        height: 70%;
        box-shadow: none;
        position: inherit;
        top: 0;
        right: 0;
        bottom: 0;
        transform: none;
        background-color: #ffffff;
        color: #fff;
        z-index: 2;
    }
}
